.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--no-scroll {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      font-weight: $bold;
      font-size: 32px;
      line-height: 32px;

      text-align: center;
      letter-spacing: -1.5px;

      color: #292929;
      margin: 0 0 48px;

      @include onTablet {
        margin: 0 0 64px;
        font-size: 40px;
        line-height: 40px;
      }

      @include onDesktop {
        margin: 0 0 64px;
        font-size: 48px;
        line-height: 48px;
      }

      &-third {
        text-align: left;
      }
    }
  }
}
